<template>
  <div class="ticket__style">
      <div>Ticket for Charity Dinner</div>
      <div class="ticket__price">$ 500</div>
      <div>It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout.</div>
      <div class="ticket__of">5 of 60 Claimed</div>
      <ul>
          <li v-for="{id, text} in fruits" :key="id">
            {{ text }}
          </li>
      </ul>
  </div>
</template>

<script>
export default {
    data() {
        return {
            fruits: [
              {
                id: 1,
                text: 'Universal Chef'
              },
              {
                id: 2,
                text: 'Sushi'
              },
              {
                id: 3,
                text: 'Sea Food'
              }
            ]
        };
    }
};
</script>



